import Modal from "../../../../components/modal";
import React from "react";

import "./style.css";

function PrivacyPolicyModal({ onRequestClose }) {
  return (
    <Modal onRequestClose={onRequestClose}>
      <div className="title">
        Poltica de Privacidade - Semcomp USP
        <br />
        <span style={{ fontSize: "0.8rem" }}>
          <em>Esta poltica  vlida a partir de Agosto de 2021.</em>
        </span>
      </div>
      <div className="content">
        <p>
          A Semcomp USP leva a sua privacidade a srio e zela pela segurana e
          proteo de dados de todos os seus parceiros e usurios do site
          domnio{" "}
          <a href="https://semcomp.icmc.usp.br/">
            https://semcomp.icmc.usp.br/
          </a>
          .
        </p>
        <p>
          Esta Poltica de Privacidade destina-se a inform-lo sobre o modo como
          ns utilizamos e divulgamos informaes coletadas em suas visitas ao
          nossa site e em mensagens que trocamos com voc.
        </p>
        <p>
          Esta Poltica de Privacidade aplica-se somente a informaes coletadas
          por meio do site.
        </p>
        <p>
          <strong>
            AO ACESSAR O SITE, ENVIAR COMUNICAES OU FORNECER QUALQUER TIPO DE
            DADO PESSOAL, VOC DECLARA ESTAR CIENTE COM RELAO AOS TERMOS AQUI
            PREVISTOS E DE ACORDO COM A POLTICA DE PRIVACIDADE, A QUAL DESCREVE
            AS FINALIDADES E FORMAS DE TRATAMENTO DE SEUS DADOS PESSOAIS QUE
            VOC DISPONIBILIZAR NO SITE.
          </strong>
        </p>
        <p>
          Esta Poltica de Privacidade fornece uma viso geral de nossas
          prticas de privacidade e das escolhas que voc pode fazer, bem como
          direitos que voc pode exercer em relao aos Dados Pessoais tratados
          por ns. Se voc tiver alguma dvida sobre o uso de Dados Pessoais,
          entre em contato com{" "}
          <a href="https://www.notion.so/semcomp-icmc-usp-br-7ae739c777c84d3c95cd509b54e31ad6">
            semcomp@icmc.usp.br
          </a>
          .
        </p>
        <p>
          Alm disso, a Poltica de Privacidade no se aplica a quaisquer
          aplicativos, produtos, servios, site ou recursos de mdia social de
          terceiros que possam ser oferecidos ou acessados por meio do site. O
          acesso a esses links far com que voc deixe o nosso site e poder
          resultar na coleta ou compartilhamento de informaes sobre voc por
          terceiros. Ns no controlamos, endossamos ou fazemos quaisquer
          representaes sobre sites de terceiros ou suas prticas de
          privacidade, que podem ser diferentes das nossas. Recomendamos que
          voc revise a poltica de privacidade de qualquer site com o qual voc
          interaja antes de permitir a coleta e o uso de seus Dados Pessoais.
        </p>
        <p>
          Caso voc nos envie Dados Pessoais referentes a outras pessoas
          fsicas, voc declara ter a competncia para faz-lo e declara ter
          obtido o consentimento necessrio para autorizar o uso de tais
          informaes nos termos desta Poltica de Privacidade.
        </p>
        <h1>
          <strong>Seo 1 - Definies</strong>
        </h1>
        <p>Para os fins desta Poltica de Privacidade:</p>
        <ol className="document-ordered-list">
          <li>
            &quot;<strong>Dados Pessoais</strong>&quot;: significa qualquer
            informao que, direta ou indiretamente, identifique ou possa
            identificar uma pessoa natural, como por exemplo, nome, CPF, data de
            nascimento, endereo IP, nmero USP, dentre outros;
          </li>

          <li>
            &quot;<strong>Dados Pessoais Sensveis</strong>&quot;: significa
            qualquer informao que revele, em relao a uma pessoa natural,
            origem racial ou tnica, convico religiosa, opinio poltica,
            filiao a sindicato ou a organizao de carter religioso,
            filosfico ou poltico, dado referente  sade ou  vida sexual,
            dado gentico ou biomtrico;
          </li>

          <li>
            &quot;<strong>Tratamento de Dados Pessoais</strong>&quot;: significa
            qualquer operao efetuada no mbito dos Dados Pessoais, por meio de
            meios automticos ou no, tal como a recolha, gravao, organizao,
            estruturao, armazenamento, adaptao ou alterao, recuperao,
            consulta, utilizao, divulgao por transmisso, disseminao ou,
            alternativamente, disponibilizao, harmonizao ou associao,
            restrio, eliminao ou destruio. Tambm  considerado Tratamento
            de Dados Pessoais qualquer outra operao prevista nos termos da
            legislao aplicvel;
          </li>

          <li>
            &quot;<strong>Leis de Proteo de Dados</strong>&quot;: significa
            todas as disposies legais que regulam o Tratamento de Dados
            Pessoais, incluindo, porm sem se limitar, a Lei n 13.709/18, Lei
            Geral de Proteo de Dados Pessoais (&quot;<strong>LGPD</strong>
            &quot;).
          </li>
        </ol>
        <h1>
          <strong>Seo 2 - Uso de Dados Pessoais</strong>
        </h1>
        <p>
          Coletamos e usamos Dados Pessoais para gerenciar seu relacionamento
          conosco e melhor atend-lo quando voc estiver utilizando servios do
          site, personalizando e melhorando sua experincia. Exemplos de como
          usamos os dados incluem:
        </p>
        <ol className="document-ordered-list">
          <li>Gerar certificados de participao;</li>

          <li>Enviar informaes relevantes acerca do evento;</li>

          <li>Personalizar sua experincia de uso do site;</li>

          <li>
            Entender melhor o pblico alvo a fim de promover um evento mais bem
            direcionado;
          </li>

          <li>Promover um evento mais acessvel e inclusivo;</li>

          <li>
            Gerir a participao em eventos de cunho cultural ao longo do
            evento, tais quais a GameNight e os concursos;
          </li>

          <li>
            Para entrarmos em contato pelo usurio do Discord e/ou endereo de
            e-mail e/ou usurio do Telegram fornecido. Podemos entrar em contato
            com voc pessoalmente, por mensagem de voz, ou por e-mail, nos
            termos da lei e para fins razoveis.
          </li>
        </ol>
        <p>
          Alm disso, os Dados Pessoais fornecidos tambm podem ser utilizados
          na forma que julgarmos necessria ou adequada: <strong>(a)</strong>{" "}
          nos termos das Leis de Proteo de Dados; <strong>(b)</strong> para
          atender exigncias de processo judicial; <strong>(c)</strong> para
          cumprir deciso judicial, deciso regulatria ou deciso de
          autoridades competentes, incluindo autoridades fora do pas de
          residncia; <strong>(d)</strong> para aplicar nossos Termos e
          Condies de Uso; <strong>(e)</strong> para proteger nossas operaes;{" "}
          <strong>(f)</strong> para proteger direitos, privacidade, segurana
          nossos, seus ou de terceiros; <strong>(g)</strong> para detectar e
          prevenir fraude; <strong>(h)</strong> permitir-nos usar as aes
          disponveis ou limitar danos que venhamos a sofrer; e (i) de outros
          modos permitidos por lei.
        </p>
        <h1>
          <strong>Seo 3 - No fornecimento de Dados Pessoais</strong>
        </h1>
        <p>
          No h obrigatoriedade em compartilhar os Dados Pessoais que
          solicitamos. No entanto, se voc optar por no os compartilhar, em
          alguns casos, no poderemos fornecer a voc acesso completo ao site,
          alguns recursos especializados ou ser capaz de prestar a assistncia
          necessria ou, ainda, viabilizar a prestao o servio em questo.
        </p>
        <h1>
          <strong>Seo 4 - Dados coletados</strong>
        </h1>
        <p>
          O pblico em geral poder navegar no site sem necessidade de qualquer
          cadastro e envio de Dados Pessoais. No entanto, algumas das
          funcionalidades do site podero depender de cadastro e envio de Dados
          Pessoais como participar de minicursos e/ou realizar inscrio em
          eventos especficos e/ou receber certificado de participao.
        </p>
        <p>No contato, ns podemos coletar:</p>
        <ol className="document-ordered-list">
          <li>
            <strong>Dados de contato:</strong> nome, sobrenome, endereo de
            e-mail, usurio do Telegram, usurio do Discord, curso e faculdade;
          </li>

          <li>
            <strong>Dados Pessoais Sensveis:</strong> informaes acerca de
            Sade, em especfico em relao a Pessoas com Deficincia (PCD);
          </li>

          <li>
            <strong>Informaes enviadas:</strong> informaes que voc envia
            atravs de formulrio (dvidas, reclamaes, sugestes, crticas,
            elogios etc.).
          </li>
        </ol>
        <p>Na navegao geral no site, ns poderemos coletar:</p>
        <ol className="document-ordered-list">
          <li>
            <strong>Dados de navegao no site:</strong> informaes sobre suas
            visitas e atividades, incluindo o contedo (e quaisquer anncios)
            com os quais voc visualiza e interage, informaes sobre o
            navegador e o dispositivo que voc est usando, seu endereo IP, sua
            localizao, o endereo do site a partir do qual voc chegou.
            Algumas dessas informaes so coletadas usando nossas Ferramentas
            de Coleta Automtica de Dados, que incluem cookies, web beacons e
            links da web incorporados. Para saber mais, leia como ns usamos
            Ferramentas de Coleta Automtica de Dados na seo 7 abaixo;
          </li>

          <li>
            <strong>Outras informaes que podemos coletar:</strong> informaes
            que no revelem especificamente a sua identidade ou que no so
            diretamente relacionadas a um indivduo, tais como informaes sobre
            navegador e dispositivo; dados de uso do site; e informaes
            coletadas por meio de cookies, pixel tags e outras tecnologias.
          </li>
        </ol>
        <h1>
          <strong>
            Seo 5 - Compartilhamento de Dados Pessoais com terceiros
          </strong>
        </h1>
        <p>Ns poderemos compartilhar seus Dados Pessoais:</p>
        <ol className="document-ordered-list">
          <li>
            Com a(s) empresa(s) parceira(s) caso voc selecione ou opte por
            enviar os seus dados. Essas empresas podem estar localizadas no
            Brasil, nos Estados Unidos ou em outros locais globais e podem
            utiliz-los para personalizao de contedo, atividades de
            publicidade e marketing (incluindo publicidade digital
            personalizada) e servios de TI, por exemplo. Com elas, sero
            compartilhados nome, email, faculdade e curso.
          </li>
        </ol>
        <h1>
          <strong>Seo 6 - Transferncias internacionais de dados</strong>
        </h1>
        <p>
          Dados Pessoais e informaes de outras naturezas coletadas por ns
          podem ser transferidos ou acessados por entidades pertencentes ao
          grupo corporativo das empresas parceiras em todo o mundo de acordo com
          esta Poltica de Privacidade.
        </p>
        <h1>
          <strong>Seo 7 - Coleta automtica de Dados Pessoais</strong>
        </h1>
        <p>
          Quando voc visita o site, ele pode armazenar ou recuperar informaes
          em seu navegador, principalmente na forma de cookies, que so arquivos
          de texto contendo pequenas quantidades de informao. Essas
          informaes podem ser sobre voc, suas preferncias ou seu dispositivo
          e so usadas principalmente para que o site funcione como voc espera.
          As informaes geralmente no o identificam diretamente, mas podem
          oferecer uma experincia na internet mais personalizada.
        </p>
        <p>
          De acordo com esta Poltica de Privacidade, ns e nossos prestadores
          de servios terceirizados, mediante seu consentimento, podemos coletar
          seus Dados Pessoais de diversas formas, incluindo, entre outros:
        </p>
        <ol className="document-ordered-list">
          <li>
            <strong>Por meio do navegador ou do dispositivo:</strong> algumas
            informaes so coletadas pela maior parte dos navegadores ou
            automaticamente por meio de dispositivos de acesso  internet, como
            o tipo de computador, resoluo da tela, nome e verso do sistema
            operacional, modelo e fabricante do dispositivo, idioma, tipo e
            verso do navegador de Internet que est utilizando. Podemos
            utilizar essas informaes para assegurar que o site funcione
            adequadamente.
          </li>

          <li>
            <strong>Uso de cookies:</strong> informaes sobre o seu uso do site
            podem ser coletadas por terceiros a partir de cookies. Cookies so
            informaes armazenadas diretamente no computador que voc est
            utilizando. Os cookies permitem a coleta de informaes tais como o
            tipo de navegador, o tempo despendido no site, as pginas visitadas,
            as preferncias de idioma, e outros dados de trfego annimos. Ns e
            nossos prestadores de servios utilizamos informaes para proteo
            de segurana, para facilitar a navegao, exibir informaes de modo
            mais eficiente, e personalizar sua experincia ao utilizar o site,
            assim como para rastreamento online. Tambm coletamos informaes
            estatsticas sobre o uso do site para aprimoramento contnuo do
            nosso design e funcionalidade, para entender como o site  utilizada
            e para auxili-lo a solucionar questes relativas ao site. Caso no
            deseje que suas informaes sejam coletadas por meio de cookies, h
            um procedimento simples na maior parte dos navegadores que permite
            que os cookies sejam automaticamente rejeitados, ou oferece a opo
            de aceitar ou rejeitar a transferncia de um cookie (ou cookies)
            especfico(s) de um site determinado para o seu computador.
            Entretanto, isso pode gerar inconvenientes no uso do site. As
            definies que escolher podem afetar a sua experincia de navegao
            e o funcionamento que exige a utilizao de cookies. Neste sentido,
            rejeitamos qualquer responsabilidade pelas consequncias resultantes
            do funcionamento limitado do site provocado pela desativao de
            cookies no seu dispositivo (incapacidade de definir ou ler um
            cookie).
          </li>

          <li>
            <strong>Uso de pixel tags e outras tecnologias similares:</strong>{" "}
            pixel tags (tambm conhecidos como Web beacons e GIFs invisveis)
            podem ser utilizados para rastrear aes de usurios do site
            (incluindo destinatrios de e-mails), medir o sucesso das nossas
            campanhas de marketing e coletar dados estatsticos sobre o uso do
            site e taxas de resposta, e ainda para outros fins no
            especificados. Podemos contratar empresas de publicidade
            comportamental, para obter relatrios sobre os anncios do site em
            toda a internet. Para isso, essas empresas utilizam cookies, pixel
            tags e outras tecnologias para coletar informaes sobre a sua
            utilizao, ou sobre a utilizao de outros usurios, do nosso site
            e de site de terceiros. Ns no somos responsveis por pixel tags,
            cookies e outras tecnologias similares utilizadas por terceiros.
          </li>
        </ol>
        <h1>
          <strong>Seo 8 - Categorias de cookies</strong>
        </h1>
        <p>
          Os cookies utilizados no nosso site esto de acordo com os requisitos
          legais e so enquadrados nas seguintes categorias:
        </p>
        <ol className="document-ordered-list">
          <li>
            <strong>Estritamente necessrios:</strong> estes cookies permitem
            que voc navegue pelo site e desfrute de recursos essenciais com
            segurana. Um exemplo so os cookies de segurana, que autenticam os
            usurios, protegem os seus dados e evitam a criao de logins
            fraudulentos.
          </li>

          <li>
            <strong>Desempenho:</strong> os cookies desta categoria coletam
            informaes de forma codificada e annima relacionadas ao site,
            como, por exemplo, o nmero de visitantes de uma pgina especfica,
            origem das visitas ao site e quais as pginas acessadas pelo
            usurio. Todos os dados coletados so utilizados apenas para
            eventuais melhorias no site e para medir a eficcia da nossa
            comunicao.
          </li>

          <li>
            <strong>Funcionalidade:</strong> estes cookies so utilizados para
            lembrar definies de preferncias do usurio com o objetivo de
            melhorar a sua visita no nosso site, como, por exemplo,
            configuraes aplicadas no layout do site ou suas respostas para
            pop-ups de promoes e cadastros -; dessa forma, no ser necessrio
            perguntar inmeras vezes.
          </li>

          <li>
            <strong>Publicidade:</strong> utilizamos cookies com o objetivo de
            criar campanhas segmentadas e entregar anncios de acordo com o seu
            perfil de consumo no nosso site.
          </li>
        </ol>
        <h1>
          <strong>Seo 9 - Direitos do Usurio</strong>
        </h1>
        <p>
          Voc pode, a qualquer momento, requerer: <strong>(i)</strong>{" "}
          confirmao de que seus Dados Pessoais esto sendo tratados;{" "}
          <strong>(ii)</strong> acesso aos seus Dados Pessoais;{" "}
          <strong>(iii)</strong> correes a dados incompletos, inexatos ou
          desatualizados; <strong>(iv)</strong> anonimizao, bloqueio ou
          eliminao de dados desnecessrios, excessivos ou tratados em
          desconformidade com o disposto em lei; <strong>(v)</strong>{" "}
          portabilidade de Dados Pessoais a outro prestador de servios,
          contanto que isso no afete nossos segredos industriais e comerciais;{" "}
          <strong>(vi)</strong> eliminao de Dados Pessoais tratados com seu
          consentimento, na medida do permitido em lei; <strong>(vii)</strong>{" "}
          informaes sobre as entidades s quais seus Dados Pessoais tenham
          sido compartilhados; <strong>(viii)</strong> informaes sobre a
          possibilidade de no fornecer o consentimento e sobre as consequncias
          da negativa; e <strong>(ix)</strong> revogao do consentimento. Os
          seus pedidos sero tratados com especial cuidado de forma a que
          possamos assegurar a eficcia dos seus direitos. Poder lhe ser pedido
          que faa prova da sua identidade de modo a assegurar que a partilha
          dos Dados Pessoais  apenas feita com o seu titular.
        </p>
        <p>
          Voc dever ter em mente que, em certos casos (por exemplo, devido a
          requisitos legais), o seu pedido poder no ser imediatamente
          satisfeito, alm de que ns poderemos no conseguir atend-lo por
          conta de cumprimento de obrigaes legais.
        </p>
        <h1>
          <strong>Seo 10 - Segurana dos Dados Pessoais</strong>
        </h1>
        <p>
          Buscamos adotar as medidas tcnicas e organizacionais previstas pelas
          Leis de Proteo de Dados adequadas para proteo dos Dados Pessoais
          na nossa organizao. Infelizmente, nenhuma transmisso ou sistema de
          armazenamento de dados tem a garantia de serem 100% seguros. Caso
          tenha motivos para acreditar que sua interao conosco tenha deixado
          de ser segura (por exemplo, caso acredite que a segurana de qualquer
          uma de suas contas foi comprometida), favor nos notificar
          imediatamente.
        </p>
        <h1>
          <strong>
            Seo 11 - Links de hipertexto para outros sites e redes sociais
          </strong>
        </h1>
        <p>
          O site poder, de tempos a tempos, conter links de hipertexto que
          redirecionar voc para sites das redes dos nossos parceiros,
          anunciantes, fornecedores etc. Se voc clicar em um desses links para
          qualquer um desses sites, lembre-se que cada site possui as suas
          prprias prticas de privacidade e que no somos responsveis por
          essas polticas. Consulte as referidas polticas antes de enviar
          quaisquer Dados Pessoais para esses sites.
        </p>
        <p>
          No nos responsabilizamos pelas polticas e prticas de coleta, uso e
          divulgao (incluindo prticas de proteo de dados) de outras
          organizaes, tais como Facebook, Apple, Google, Microsoft, ou de
          qualquer outro desenvolvedor de software ou provedor de aplicativo,
          loja de mdia social, sistema operacional, prestador de servios de
          internet sem fio ou fabricante de dispositivos, incluindo todos os
          Dados Pessoais que divulgar para outras organizaes por meio dos
          aplicativos, relacionadas a tais aplicativos, ou publicadas em nossas
          pginas em mdias sociais. Ns recomendamos que voc se informe sobre
          a poltica de privacidade de cada site visitado ou de cada prestador
          de servio utilizado.
        </p>
        <h1>
          <strong>Seo 12 - Atualizaes desta Poltica de Privacidade</strong>
        </h1>
        <p>
          Se modificarmos nossa Poltica de Privacidade, publicaremos o novo
          texto no site, com a data de reviso atualizada. Podemos alterar esta
          Poltica de Privacidade a qualquer momento. Caso haja alterao
          significativa nos termos desta Poltica de Privacidade, podemos
          inform-lo por meio das informaes de contato que tivermos em nosso
          banco de dados ou por meio de notificao em nosso site.
        </p>
        <p>
          Recordamos que ns temos como compromisso no tratar os seus Dados
          Pessoais de forma incompatvel com os objetivos descritos acima,
          exceto se de outra forma requerido por lei ou ordem judicial.
        </p>
        <p>
          Sua utilizao da site aps as alteraes significa que aceitou as
          Polticas de Privacidade revisadas. Caso, aps a leitura da verso
          revisada, voc no esteja de acordo com seus termos, favor encerrar o
          acesso ao site.
        </p>
        <h1>
          <strong>
            Seo 13 - Encarregado do tratamento dos Dados Pessoais
          </strong>
        </h1>
        <p>
          Caso pretenda exercer qualquer um dos direitos previstos, inclusive
          retirar o seu consentimento, nesta Poltica de Privacidade e/ou nas
          Leis de Proteo de Dados, ou resolver quaisquer dvidas relacionadas
          ao Tratamento de seus Dados Pessoais, favor contatar-nos em{" "}
          <a href="https://www.notion.so/semcomp-icmc-usp-br-82a7b27fe9074df1a433cdde80bf7ce1">
            semcomp@icmc.usp.br
          </a>
          .
        </p>
      </div>
      <button className="cancel" type="button" onClick={onRequestClose}>
        Fechar
      </button>
    </Modal>
  );
}

export default PrivacyPolicyModal;
