import React from "react";

import Modal from "../../../components/modal";
import "./style.css";

function AboutOverflow({ onRequestClose }) {
  return (
    <Modal onRequestClose={onRequestClose}>
      <div className="title">Sobre o Overflow</div>
      <div className="content">
        O Semcomp Overflow  o principal concurso da nossa semana!  uma taa
        das casas onde quatro diferentes equipes competem pela vitria! Todos os
        participantes inscritos no site da Semcomp so lidos pelo nosso chapu
        seletor e selecionados para um dos seguintes times -{" "}
        <strong>Agamotto, DeLorean, Ocarina ou Tardis</strong> - e a partir de
        ento estaro competindo por pontos, atravs de todos os nossos eventos.
        A diviso  feita de forma uniforme, para que todos os times tenham um
        nmero parecido de participantes.
        <br />
        <br />
        E para deixar o jogo mais interessante Que tal novidades? <br />
        <br />
        Pela primeira vez em todas as edies da Semcomp, voc e sua equipe
        podero visualizar e competir por diferentes conquistas, todas
        disponveis no nosso site. <br />
        <br />
        Ento ative seu esprito de trabalho em equipe e junte-se aos seus
        colegas nessa jornada divertida do Overflow! No se esquea de entrar no
        grupo do Telegram da sua casa, o link para convite est disponvel no
        site da Semcomp! <br />
        <br />
        <strong>&gt;&gt;&gt; Como jogar</strong>
        <br />
        <strong>
          Participar das palestras, rodas de conversas, e eventos da semana:
        </strong>{" "}
        Quanto mais participantes da mesma casa estiverem online nas palestras,
        mais pontos voc fatura, ento no deixe de ir em nenhuma! Alm disso, a
        casa que garantir mais participantes por palestra receber conquistas
        exclusivas no nosso site!
        <br />
        <br />
        <strong>Competir nos outros concursos:</strong> Todos os vencedores dos
        concursos da semana alm de receber prmios, tambm levam pontos e
        conquistas! A Gamenight tambm est inclusa, ento aproveite para se
        inscrever nos campeonatos!
        <br />
        <br />
        <strong>Gincanas Atemporais:</strong> de segunda a quarta, no perca as
        lives na Twitch e represente sua casa nas gincanas, fique ligado!
        <br />
        <br />
        <strong>Encerramento:</strong> No nosso tradicional encerramento, em que
        temos os eventos finais da semana, vamos ter mais chances para os
        participantes ganharem pontos, atravs de alguns mini desafios entre
        eles! No final, vamos anunciar os pontos recebidos por cada casa, a
        pontuao final e a casa vencedora! Vale lembrar que todos da equipe
        vencedora ganharo uma lembrana da to honrada conquista.
      </div>
      <button className="cancel" type="button" onClick={onRequestClose}>
        Fechar
      </button>
    </Modal>
  );
}

export default AboutOverflow;
