import React from "react";

// import { Container } from './styles';

function ContestRegistration({ updateFormValue, registerTeam }) {
  const teamNameRef = React.useRef();
  const scholarshipRef = React.useRef();
  const courseRef = React.useRef();
  const entranceRef = React.useRef();

  const technicalExpRef = React.useRef();
  const resolutionExpRef = React.useRef();
  const contestExpRef = React.useRef();
  const categoryRef = React.useRef();

  const [category, setCategory] = React.useState(0);
  const [categoryDisabled, setCategoryDisabled] = React.useState(true);

  function handleFormUpdate() {
    // Get the input's values from their refs.
    const teamName = teamNameRef.current?.value;
    const scholarship = scholarshipRef.current.value;
    const course = courseRef.current.value;
    const entrance = entranceRef.current.value;

    const technicalExp = technicalExpRef.current.value;
    const resolutionExp = resolutionExpRef.current.value;
    const contestExp = contestExpRef.current.value;
    const category = categoryRef.current.value;

    // Define user's categories from their experience
    setCategory(
      Math.floor(
        (parseInt(technicalExp) +
          parseInt(resolutionExp) +
          parseInt(contestExp)) /
          3
      )
    );

    // Only enable category combobox after user answering all levelling questions
    setCategoryDisabled(!technicalExp || !resolutionExp || !contestExp);

    updateFormValue({
      teamName,
      scholarship,
      course,
      entrance,
      technicalExp,
      resolutionExp,
      contestExp,
      category,
    });
  }

  return (
    <div className="contest-registration-container">
      {registerTeam && (
        <label>
          <p>
            Qual o nome da sua equipe?
            <br />
            <span className="information">
              Todos os trs integrantes precisam colocar o mesmo nome da equipe!
            </span>
          </p>
          <input type="text" ref={teamNameRef} onChange={handleFormUpdate} />
        </label>
      )}
      <label>
        <p>Qual a sua escolaridade?</p>
        <select ref={scholarshipRef} onChange={handleFormUpdate}>
          <option value=""></option>
          <option value="Ensino fundamental incompleto">
            Ensino fundamental incompleto
          </option>
          <option value="Ensino fundamental completo">
            Ensino fundamental completo
          </option>
          <option value="Ensino mdio incompleto">
            Ensino mdio incompleto
          </option>
          <option value="Ensino mdio completo">Ensino mdio completo</option>
          <option value="Ensino superior incompleto">
            Ensino superior incompleto
          </option>
          <option value="Ensino superior completo">
            Ensino superior completo
          </option>
          <option value="Ps-graduao incompleta">
            Ps-graduao incompleta
          </option>
          <option value="Ps-graduao completa">Ps-graduao completa</option>
        </select>
      </label>
      <label>
        <p>Se voc est na universidade ou j se formou, qual o seu curso?</p>
        <input type="text" ref={courseRef} onChange={handleFormUpdate} />
      </label>
      <label>
        <p>
          Se voc est na universidade, qual o seu ano de ingresso no seu curso
          atual?
        </p>
        <input type="text" ref={entranceRef} onChange={handleFormUpdate} />
      </label>

      <label>
        <p>
          Quantos desses assuntos voc conhece e sabe o que ?
          <br />
          <span className="information">
            Pilha, Fila, Lista Ligada, rvore Binria de Busca, Hashmap,
            Programao Dinmica, Algoritmo de Ordenao, BFS/DFS, Dijkstra,
            Manipulao de Strings, Recurso/Backtracking, Teoria dos Nmeros
          </span>
        </p>
        <select ref={technicalExpRef} onChange={handleFormUpdate}>
          <option value=""></option>
          <option value="1">At 2 assuntos</option>
          <option value="2">De 3 a 6 assuntos</option>
          <option value="3">Acima de 7 assuntos</option>
        </select>
      </label>
      <label>
        <p>
          Qual o seu nvel de experincia com resoluo de problemas de
          programao competitiva?
        </p>
        <select ref={resolutionExpRef} onChange={handleFormUpdate}>
          <option value=""></option>
          <option value="1">No sei nada ou sei pouco</option>
          <option value="2">Tenho alguma experincia</option>
          <option value="3">Tenho bastante experincia</option>
        </select>
      </label>
      <label>
        <p>J participou de algum outro desafio/maratona de programao?</p>
        <select ref={contestExpRef} onChange={handleFormUpdate}>
          <option value=""></option>
          <option value="1">No, vai ser minha primeira vez!</option>
          <option value="2">J, uma ou duas vezes</option>
          <option value="3">J, pelo menos trs vezes</option>
        </select>
      </label>
      <label>
        <p>
          Baseado nas suas respostas, voc se qualifica para as seguintes
          categorias. Em qual delas voc gostaria de se inscrever?
        </p>
        <select
          className={categoryDisabled ? "disabled" : ""}
          ref={categoryRef}
          onChange={handleFormUpdate}
          disabled={categoryDisabled}
        >
          <option value=""></option>
          <option value="Just 4 fun">
            Just 4 fun - Categoria casual, sem restrio de nvel de
            conhecimento
          </option>
          {category <= 1 && <option value="Iniciante">Iniciante</option>}
          {category <= 2 && (
            <option value="Intermedirio">Intermedirio</option>
          )}
          {category <= 3 && <option value="Profissional">Profissional</option>}
        </select>
      </label>
    </div>
  );
}

export default ContestRegistration;
